import React, { Component } from 'react';
import {Link} from 'react-router-dom'

import './Navbar.css'
import StarButton from '../buttonStart/StarButton'
import Menu from './menu/Menu'
import LogoNavbar from './logoNavbar/LogoNavbar'
import StartButton from '../buttonStart/StarButton';




class Navbar extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            width: window.innerWidth
         }
    }
    componentWillMount() {
        window.addEventListener('resize', this.handleWindowSizeChange);
      }
      
      // make sure to remove the listener
      // when the component is not mounted anymore
      componentWillUnmount() {
        window.removeEventListener('resize', this.handleWindowSizeChange);
      }
      
      handleWindowSizeChange = () => {
        this.setState({ width: window.innerWidth });
      };
    render() {
        const { width } = this.state;
        const isMovile = width <= 600;
        //estilos
        
        if(isMovile){
            return (    
                <div className="header-navbar">


                <div className="navbarDiv1">
                    <Menu className="movile" on />
   
                </div>
                <div className="navbarDiv2">
                    <LogoNavbar />
        
                </div>       
                <div className="navbarDiv3">
                    <StartButton/>
        
                </div>
            

       </div> );

        } else {
            //desktop version ------->
            return (    
                <div className="header-navbar">

                    <div className="navbarDiv1">
                        <LogoNavbar/>   

                    </div>
                    <div className="navbarDiv2">
                        <StarButton/>
            
                    </div>
                    <div className="navbarDiv3">
                        <Menu/>
            
                    </div>
   
       </div> );
        }

    
    }
}
 
export default Navbar;