import React, {Component} from 'react' ;
import './Menu.css'
import icon from'../../../assets/menu.svg'
import xicon from '../../../assets/menu-x.svg'
//import icons
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';
import CloudIcon from '@material-ui/icons/Cloud';
import CodeIcon from '@material-ui/icons/Code';

//import learn code input
import LearnInput from '../../buttonLearn/learnInput'

//import link
import {Link} from 'react-router-dom'

class Menu extends Component {
    state={
        show_navbar: false,
        srcicon: icon
    };

    menu = () => {
        this.setState(current=>({show_navbar:!current.show_navbar}));
        var ligther = true
        if (this.state.srcicon===icon){
            ligther = false
        }
        switch (ligther) {
            case false:
                this.setState({srcicon:xicon});
                break;
            default:
                this.setState({srcicon:icon});
                break;
        }
        
    }
    
    closeMenu = () =>{
        this.setState({show_navbar:false})
        this.setState({srcicon:icon});
 
    }
    
    
    render(){
     
        return(
            <div>
                <button  onClick={this.menu} className="button-hamburgerIcon">
                    <img className="img-hamburger" src={this.state.srcicon} /></button>


                    {this.state.show_navbar ? ( 
                                <div className="div-menu">  
                                    <ul>
                                        <li>
                                            <a className="suport" href=""> <HelpOutlineIcon className="icons-menu" /> Support </a>
                                        </li>
                                        <li>
                                            <a className="resources"href=""> <CodeIcon className="icons-menu"/> Resources</a>
                                        </li>
                                        <li className="learn-input">
                                            
                                           <Link to="/courses" onClick={this.closeMenu}> <container className="container-component"> <LearnInput/> </container>  </Link> 

                                        </li>
                                        <li>
                                            <a className="morecontent" href=""> 
                                            <CloudIcon className="icons-menu" />
                                            More content 
                                            </a>
                                        </li>
                                        <li>
                                            <Link className="about" to="/about" onClick={this.closeMenu}> About </Link>
                                        </li>
                                        <li>
                                            <a className="donate" href=""> Donate</a>
                                        </li>
                                    </ul>
                                    <div className="separador" onClick={this.closeMenu}></div>
                                </div>):( null )}
            </div>
        );
    }
}
export default Menu